import React, { Component } from "react";
import "./ToDoApp.css";

export class TodoApp extends Component {
  state = {
    input: "",
    items: [],
  };

  onAdd = (e) => {
    this.setState({
      input: e.target.value,
    });
  };
  storeItem = (e) => {
    e.preventDefault();
    const { input } = this.state;
    //const allItem = this.state.items;
    //allItem.push(input);
    this.setState({
      items: [...this.state.items, input],
      input: "",
    });

  };
  deleteItem = (id) => {
    // const allItem=this.state.items;
    // allItem.slice(id,1);
    this.setState({
      //   items:allItem
      items: this.state.items.filter((data, index) => index !== id),
    });
  };
  editItem=(id)=>{
    
  }

  render() {
    const { input, items } = this.state;

    return (
      <div className="todo-container">
        <form className="input-sec" onSubmit={this.storeItem}>
          <h1>TodoApp</h1>
          <input
            type="text"
            value={input}
            onChange={this.onAdd}
            placeholder="Enter item...."
          />
        </form>
        <ul>
          {items.map((data, index) => (
            <li  key={index} >
              {data}
              <div className="icon">
              <i  className="fas fa-edit" onClick={() => this.editItem(index)}></i>
              <i className="fas fa-trash"onClick={() => this.deleteItem(index)}></i>
              </div>
            </li>
          ))}
        </ul>
      </div>
    );
  }
}
export default TodoApp;
